body{
    background-color: aquamarine;
    box-sizing: border-box;
    margin: 0;
    padding: 0%;
    font-family: "Josefin Sans", sans-serif;
}

div{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -30px;
}
h1{
    background-color: #ffe9c5;
    color: brown;
    border-radius: 20px;
    padding: 30px 30px;
}
span{
    color: #eb6383;
}